.App {
  text-align: center;
}
.App-logo {
  display: flex;
  height: 40vmin;
  pointer-events: none;
  width: 15%;  
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(3000deg);
  }
}
